'use client'
import { useEffect, useState } from "react";
import BlogSection from "@/components/Blog";
import FixedDepartures from "@/components/FixedDepartures";
import Footer from "@/components/Footer";
import Gallery from "@/components/Gallery";
import Navbar from "@/components/Navbar";
import PackagesHeroSection from "@/components/PackagesHeroSection";
import TopPackages from "@/components/TopPackages";

const Packages = () => {
  const [trips, setTrips] = useState([]);

  useEffect(() => {
    const fetchTrips = async () => {
      try {
        const response = await fetch('/api/trips');
        const data = await response.json();
        
        // Transform the data to match the expected format
        const transformedData = data.map(trip => ({
          id: trip._id,
          title: trip.title.toUpperCase(),
          image: trip.images[0] || "https://images.pexels.com/photos/163037/london-street-phone-cabin-163037.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=2",
          description: `${trip.duration.days || ''} days in ${trip.destinationCountry}`,
          discount: "",
        }));
        
        setTrips(transformedData);
      } catch (error) {
        console.error('Error fetching trips:', error);
      }
    };

    fetchTrips();
  }, []);

  return (
    <div>
      <Navbar />
      <PackagesHeroSection />
      <TopPackages />
      <Gallery />
      <BlogSection />
      <FixedDepartures
        title="Available Packages"
        description="Handpicked Getaways for Every Traveler"
        destinations={trips}
      />
      <Footer />
    </div>
  );
};

export default Packages;